body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


body{
  background-color: #51dc9f;
  background-image: linear-gradient(141deg, #51dc9f 0%, #21c9da 51%, #2ce8e8 75%);
  height: 100%;
  min-height: 100vh;
}
.App .pageContainer{
  display: flex;
  margin: 10px 150px;
}
.App .flex-33{
  flex: 33% 1;
}
.App .flex-66{
  flex: 66% 1;
}

.Home > .nav{
    display: flex;
    justify-content: flex-end;
    align-items: center;
    height: 60px;
    background-color: white;
}
.Home > .nav  button{
    width: 200px;
}
.Home .pageContainer{
    display: flex;
    margin: 10px 150px;
}

.Home .flex-33{
    flex: 33% 1;
}
.Home .flex-66{
    flex: 66% 1;
}
.Home .userInfoContainer{
    border: 1px solid #e2e2e2;
    background-color: white;
    padding: 10px 12px;
    border-radius: 5px;
}
.Home .userInfoContainer > h2{
    text-align: center;
}
.Home .userDetailContainer{
    display: flex;
}
.Home .width-50{
    width: 50%;
}
.Home .tweetsContainer{
    overflow-y: auto;
}
.Home .peopleToFollowContainer{
    overflow-y: auto;
}

.Tweet{
    display: flex;
    border: 1px solid #878786;
    background-color: white;
    padding: 10px 12px;
    border-radius: 5px;
    margin: 6px;
}
.Tweet .initials{
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    background-color: #ffa500;
    width: 40px;
    height: 40px;
    margin-right: 10px;
    min-width: 40px;
}


.Input{
    width: 100%;
    padding: 6px;
    box-sizing: border-box;
}
.Input label{
    font-weight: bold;
    display: block;
    margin-bottom: 8px;
}
.Input .inputElement{
    outline: none;
    border: 1px solid #cccccc;
    border-radius: 4px;
    background-color: white;
    padding: 8px 10px;
    display: block;
    width: 100%;
    box-sizing: border-box;
}
.Input .inputElement:focus{
    border-color: #5f5f5f;
}
.Button{
    padding: 6px;
}
.Button button{
    width: 100%;
    box-sizing: border-box;
    padding: 8px 10px;
    border-radius: 4px;
    background-color: #ffbb00;
    border: none;
    color: white;
    font-size: 15px;
}
.Button button:hover{
    cursor: pointer;
    background-color: #f7ae11;
}

.PeopleToFollow{
    display: flex;
    border: 1px solid #e2e2e2;
    background-color: white;
    padding: 10px 12px;
    border-radius: 5px;
}
.PeopleToFollow .initials{
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    background-color: #ffa500;
    width: 40px;
    height: 40px;
    margin-right: 10px;
}
.PeopleToFollow .Button{
    padding: 0;
    width: 100px;
}
.Login{
    background-image: url(/static/media/bg-m.93fc6905.jpg);
    background-position: center;
    background-size: cover;
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
.Login > form{
    width: 350px;
}
.Login .errorText{
    padding: 5px 10px;
    margin :6px;
    /*border: 1px solid #ff9191;*/
    border-radius: 5px;
    color: red;
    background-color: rgba(255,229,229,0.7);
}
.Login .linkContainer{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.SignUp {
    background-image: url(/static/media/bg-m.93fc6905.jpg);
    background-position: center;
    background-size: cover;
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
.SignUp > form{
    width: 350px;
}
.SignUp .errorText{
    padding: 5px 10px;
    margin :6px;
    /*border: 1px solid #ff9191;*/
    border-radius: 5px;
    color: red;
    background-color: rgba(255,229,229,0.7);
}

.AllTweets{
margin: 10px 150px;
}

